@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.fpt-nganhdaotao .container {
  background: #fff;
}
.fpt-nganhdaotao .section-wrapper {
  padding: 50px 0 25px;
}
.fpt-nganhdaotao h2 {
  font-size: 35px;
  color: #333;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 30px;
}
@media (max-width: 576px) {
  .fpt-nganhdaotao h2 {
    font-size: 28px;
  }
}
.fpt-nganhdaotao .description {
  background: #f5f5f5;
  padding: 20px 40px 20px 20px;
  height: 270px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: justify;
}
.fpt-nganhdaotao .description h3 {
  text-transform: uppercase;
  color: #f26f21;
  font-size: 30px;
  line-height: 1;
  margin-bottom: 30px;
}
.fpt-nganhdaotao .description p {
  font-family: "Muli";
  color: #333;
}
.fpt-nganhdaotao .description .btn-more {
  display: inline-block;
  margin-top: 15px;
  color: #f26f21;
  border: 1px solid #f26f21;
}
.fpt-nganhdaotao .description .btn-more:hover {
  background: #f26f21;
  color: #fff;
}
@media (max-width: 1200px) {
  .fpt-nganhdaotao .description {
    height: 225px;
    padding: 20px 25px 20px 20px;
  }
  .fpt-nganhdaotao .description h3 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  .fpt-nganhdaotao .description p {
    font-size: 13.5px;
  }
  .fpt-nganhdaotao .description .btn-more {
    margin-top: 10px;
  }
}
@media (max-width: 992px) {
  .fpt-nganhdaotao .description {
    height: auto;
    padding: 0;
    padding-bottom: 30px;
  }
  .fpt-nganhdaotao .description p {
    font-size: 14px;
  }
  .fpt-nganhdaotao .description .btn-more {
    margin-top: 20px;
  }
}
.fpt-nganhdaotao figure {
  position: relative;
  transition: all 0.5s ease-in-out;
}
.fpt-nganhdaotao figure figcaption {
  position: absolute;
  bottom: 15px;
  left: 20px;
}
.fpt-nganhdaotao figure figcaption h4 {
  text-transform: uppercase;
  font-weight: 700;
  color: #fff;
  margin-bottom: 10px;
}
.fpt-nganhdaotao figure figcaption ul {
  list-style: none;
  color: #fff;
  max-height: 0;
  opacity: 0;
  transition: all 0.75s;
}
.fpt-nganhdaotao figure figcaption ul li {
  font-size: 13px;
  background: #f26f21;
  padding: 5px;
  transition: 0.3s all;
}
.fpt-nganhdaotao figure figcaption ul li + li {
  margin-top: 5px;
}
.fpt-nganhdaotao figure figcaption ul li a {
  text-decoration: none;
}
.fpt-nganhdaotao figure figcaption ul li:hover {
  background: #388abc;
  transition: 0.3s all;
}
.fpt-nganhdaotao figure figcaption ul li:hover a {
  color: #fff;
}
.fpt-nganhdaotao figure:hover figcaption ul {
  max-height: 300px;
  opacity: 1;
  transition: all 0.75s;
}
.fpt-nganhdaotao figure:hover .img:after {
  animation: shine 0.75s;
}
.fpt-nganhdaotao figure .img {
  height: 100%;
  position: relative;
  width: 100%;
  overflow: hidden;
}
.fpt-nganhdaotao figure .img:before {
  content: "";
  display: block;
  padding-top: 100%;
}
.fpt-nganhdaotao figure .img:after {
  position: absolute;
  top: 0;
  left: -175%;
  z-index: 2;
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
  transform: skewX(-30deg);
}
.fpt-nganhdaotao figure .img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
@media (max-width: 576px) {
  .fpt-nganhdaotao figure figcaption {
    left: 10px;
    bottom: 10px;
  }
  .fpt-nganhdaotao figure figcaption h4 {
    font-size: 11px;
    margin-bottom: 0;
  }
  .fpt-nganhdaotao figure figcaption ul {
    display: none;
    max-height: none;
  }
}
@keyframes shine {
  100% {
    left: 125%;
  }
}